import React, {Component} from "react";
import {connect} from "react-redux"
import OnlineUtils from "eshop/utils/OnlineUtils";
import {getStayLoveRetentionMSISDN} from "eshop/modules/auth/selectors/authorization"
import {clearStayLoveRetentionMSISDN} from "eshop/modules/auth/actions/authorization"
import {getSelectedOfferType} from "eshop/modules/configurator/selectors/filters"
import {bgColor} from "eshop/modules/configurator/selectors/metaData";
import {isAccessoryListPage, isProductDetailsPage, isProductListPage} from "eshop/modules/simfree/selectors";

class StayLoveMsisdnHeader extends Component {


    render() {

        if (this.props.selectedOfferType === "VOICE_LDF") {

            return (<div className={"l-full-row " + this.props.bgColor}>
                    <div className={"l-page-row " + this.props.bgColor}>

                        {this.props.showHeader && <div className={"l-row"}>
                            <div className={"l-col l-col-12"}>
                                <h2>Wybierz ofertę abonamentu komórkowego w Love dla Firm</h2>
                            </div>
                        </div>
                        }
                        {this.props.msisdn &&
                        <div>
                            <div className={"l-row"}>
                                <div className={"l-col l-col-9"}>
                                    <div className="o-icon-list">
                                        <div className="o-icon-list__item">
                                            <div className="o-icon-list__icon u-vertical-middle "><span
                                                className="g-icon g-icon--only g-icon--sim"></span>
                                            </div>
                                            <div className="o-icon-list__text u-vertical-middle">
                                                <p className="h4 u-no-margin">
                                                    Karta SIM z nr <span
                                                    className={"g-brand2-c"}>{OnlineUtils.formatMsisdn(this.props.msisdn)}</span>
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="o-icon-list u-padding-m u-small-padding-top-m">
                                <div className="o-icon-list__item">


                                    <div className="o-icon-list__text u-vertical-middle ">

                                        <div
                                            className={" u-padding-left-xl u-margin-left-s u-small-no-margin-left u-small-no-padding-left u-padding-right-xxl"}>
                                            <p> Chcesz dokupić nowy numer w Love dla Firm lub przedłużyć umowę, ale dla
                                                innego numeru? Wybierz "Zmień" i wybierz jeszcze raz proces.</p>
                                        </div>
                                    </div>


                                    <div className="o-icon-list__icon u-vertical-middle u-right u-no-padding-right">
                                        <button onClick={this.props.clearStayLoveRetentionMSISDN}
                                                className={"opl-btn opl-btn--secondary opl-btn--small o-btn u-right"}>Zmień
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>}
                    </div>
                </div>


            )
        }

        else {
            return null;
        }


    }
}

const mapDispatchToProps = dispatch => ({
    clearStayLoveRetentionMSISDN: () => dispatch(clearStayLoveRetentionMSISDN())

});

const mapStateToProps = state => ({
    msisdn: getStayLoveRetentionMSISDN(state),
    selectedOfferType: getSelectedOfferType(state),
    bgColor: bgColor(state),
    showHeader: !(isProductListPage(state) || isProductDetailsPage(state) || isAccessoryListPage(state))
});

export default connect(mapStateToProps, mapDispatchToProps)(StayLoveMsisdnHeader);
